<template>
    <!-- 使用 useVirtual 属性开启虚拟滚动 使用虚拟滚动时，必须要固定表格高度和行高 -->
    <!-- 1. 使用格式外层必须包含一层父级元素，且必须设置高度 2. this.$refs.plTable.setHeight() // 当窗口高度变化或者外层高度变化，就调用该方法 -->
    <div style="height: 100%;width: 100%;overflow: auto">
        <div style="height: 100%;width: 100%">
            <!--<pl-table :datas="tableData" height="400" useVirtual
            注意点： 表格插件并不需要height属性字段(跟ele不同)，因为涉及分页组件。里面会去计算高度，只需要父级外层设置高度即可
            row-height属性很重要 请看表格属性列表
            -->
            <pl-table :datas="table"
                      ref="plTable"
                      big-data-checkbox
                      fixed-columns-roll
                      header-drag-style
                      show-summary
                      :total-option="[{ label: '地址' }]"
                      use-virtual
                      :row-height="20"
                      @sort-change="sort_fn"
                      @select="Select"
                      :pt-total="100"
                      @handlePageSize="handlePage">
                <template slot="empty">
                    没有查询到符合条件的记录
                </template>
                <pl-table-column type="selection" width="55"/>
                <pl-table-column type="index" width="100" fixed/>
                <!--show-overflow-tooltip属性代表超出则内容部分给出提示框-->
                <pl-table-column
                        v-for="(item,index) in columns"
                        :key="item.id"
                        :resizable="item.resizable"
                        :show-overflow-tooltip="item.showOverflowTooltip"
                        :prop="item.prop"
                        :sortable="item.sortAbled"
                        :sort-by="item.prop"
                        :fixed="item.fixed"
                        :width="item.width">
                    <template slot="header" slot-scope="scope">
                        <span class="rowItem" >
                            <span v-if="item.searchAbled" @click.stop="clickStop">
                                <el-popover
                                        ref="popover"
                                        placement="top-start"
                                        title="根据关键词进行搜索"
                                        width="200"
                                        trigger="manual"
                                        v-model="item.searchPopoverVisibile"
                                >
                                <div>
                                     <el-input
                                             v-model="search"
                                             size="mini"
                                             placeholder="输入关键字搜索"
                                             clearable
                                             @input="handInput(item.prop)"
                                             @blur="handleBlur(index)"
                                     />

                                </div>
                                <i slot="reference" class="el-icon-search" @click="openPopoever(index)"></i>
                            </el-popover>
                            </span>

                            {{ item.label }}
                            <!--<div class="searchinput" v-if="item.sortAbled"  @click.stop="clickStop">-->
                                <!--<i class="el-icon-search"></i>-->
                            <!--<el-input-->
                            <!--v-model="search"-->
                            <!--size="mini"-->
                            <!--placeholder="输入关键字搜索"-->
                            <!--@input="search_fn(item.prop)"-->
                            <!--/>-->
                            <!--</div>-->
                        </span>

                    </template>
                    <template slot-scope="scope" >
                        <slot :name="item.prop" v-if="item.scope" :rowdata="scope.row[item.prop]"></slot>
                        <span v-else> {{ scope.row[item.prop] }}</span>
                    </template>
                </pl-table-column>
                <pl-table-column
                        align="right"
                        width="400"
                        label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" :type="action.type" v-for="(action,index) in tableAction"  @click="handleAction(scope.$index, scope.row,action.name)">{{ action.name }}</el-button>
                    </template>
                </pl-table-column>

            </pl-table>
        </div>
    </div>
</template>

<script>
    export default {
        name: "components-pl-table",
        data() {
            return {
                search:'',
                filterValue:'',
            }
        },
        props:{
            tableData:{
                type:Array,
                required: true
            },
            columns:{
                type:Array,
                required: true
            },
            tableAction:{
                type:Array,
                required: true
            }
        },
        computed:{
            table(){
                if (this.search) {
                    if(this.filterValue){
                        return this.tableData.filter(data => {
                            return  !this.search || data[this.filterValue] == this.search
                        })
                    }
                }
                // console.log(this.tableData)
                return this.tableData
                // var that = this;
                // if(that.tableData){
                //     return that.tableData.filter((data) => {
                //         return  !that.search || data[that.filterValue] == that.search
                //     });
                // }
            }
        },
        mounted () {
        },
        methods: {
            sort_fn(column, prop, order){
                this.$emit("sort-customs",column)
            },
            handInput(filter){
                this.filterValue = filter
            },
            handleBlur(index){
                this.columns[index].searchPopoverVisibile = false
            },
            clickStop(){
            },
            openPopoever(index){
                this.columns.map((item)=>{
                    item.searchPopoverVisibile = false
                })
                this.columns[index].searchPopoverVisibile = true
                console.log(this.columns[index].searchPopoverVisibile)
            },
            handleAction(index,row,name){
                this.$emit("row-action",index,row,name)
                // console.log(index,row,name)
            },
            Select(data){
                this.$emit("select",data)
                // console.log(data)
            },
            handClear(){
              this.search = ''
            },
            handlePage(page,size){
                this.$emit("page-controll",page,size)

            }

        }
    }
</script>
<style scoped>
    .el-table .cell{
        position: relative;
        overflow: auto;
    }
    .el-table-column .caret-wrapper{
        position: absolute!important;
        top: 0;
        right: 0;

    }
    .kk{
        color: red;
    }
    .searchinput{
        position: absolute;
        bottom: -4px;
        left: -25px;
        width: 20px;
    }
    .el-pagination{
        margin-top: 10px;
    }
    .rowItem{
        position: relative;
    }


</style>
<style>

    .dsada {
        background-color: red;
    }
    .el-input__inner {
        height: 25px !important;
    }
    body, html {
        overflow: auto;
        margin: 0;
        padding: 10px;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
    }
    .search_button{
        margin-top: 8px;
        text-align: center;
    }
    .el-popover__title{
        font-size: 14px;
    }
    .el-table th>.cell{
        line-height: 50px;
    }
</style>
